import React, { useEffect, useState, useContext } from 'react';
import Editor from "./editorjs"
import Axios from "axios"
import { AuthContext } from "../contexts/auth_context"
import { getApiLink } from "../lib/env.lib"

import Page404 from "./404"

import notyf from "../lib/notification"

const EditArticle = (props) => {
    console.log(props)
    const { user_id, auth_token } = useContext(AuthContext)
    const [data, setData] = useState(null)
    const [disp, setDisp] = useState(0)

    useEffect(() => {
        Axios.post(`${getApiLink()}/api/v1/getArticleById`, {
            xAuthToken: auth_token,
            article_id: props.articleId
        }).then((response) => {
            console.log(response)
            // alert(response)

            if (response.data.success) {
                // notyf.success("Loaded successfully!")
                if (response.data.message.data.author_id === user_id) {
                    setData(response.data.message.data)
                    setDisp(1)
                    document.title = "Edit Article - Lattice"
                } else {
                    setDisp(2)
                }
            }
        }).catch((e) => {
            // alert(e)
            // console.log({ response: e.response })
            setDisp(2)
            if (e) {
                console.log({ resp: e.response.data })
                if (e.response.data.error.message !== null) {
                    notyf.error("" + e.response.data.error.message)
                } else {
                    notyf.error("Error")
                }
            } else {
                notyf.error("Error")
            }
        })
    }, [])
    return (
        <>
            {(() => {
                if (disp === 1) {
                    return (
                        <>
                            <Editor mode="edit" user_id={user_id} notyf={notyf} auth_token={auth_token} articleData={data} />
                        </>
                    )
                } else if (disp === 2) {
                    return (<><Page404 /></>)
                }
            })()}
        </>
    );
}

export default EditArticle;