/* eslint-disable eqeqeq */
import React, { useContext, useRef, useState, useEffect } from 'react';

import EditorJs from 'react-editor-js';
import Axios from "axios"

import { AuthContext } from "../contexts/auth_context"
import Card from "../components/card2"
import { hexStrColor, invertColor } from "../lib/colors.lib"
import { Button, Modal } from "react-bootstrap"
import { EDITOR_JS_TOOLS } from "../lib/editorjs.dependencies"
import { getApiLink } from '../lib/env.lib';
import { Redirect } from "react-router-dom"
export const randNum = (min, max) => {
    let random = Math.round(Math.random() * (+max - +min) + +min)
    return random
}

export const getRandomBanner = () => {
    return `https://picsum.photos/id/${randNum(0, 1084)}/1200/400.jpg`
}
const Editor = (props) => {
    // let key_i = 0;
    const instanceRef = useRef(null);

    const { user_id, user_dp, user_fullname, user_username } = useContext(AuthContext)
    const [show, setShow] = useState(false)
    const [data_editor, setData] = useState(null)
    const [mode, setMode] = useState(null)
    const [api, setApi] = useState("")
    const [title, setTitle] = useState("")
    const [preview, setPreview] = useState("")
    const [redir, setRedirect] = useState(<></>)
    const [ready, setReady] = useState(false)
    const [articleId, setArticleId] = useState("")
    const [banner, setBanner] = useState("NA")
    const [article_tags, setTags] = useState([])
    const [tagsInput, setTagsInput] = useState()

    function handleTagsAddition(e) {
        let val = e.target.value
        let lastChar = val[val.length - 1] //take only the last character
        if (lastChar === ' ') {//check if the last character is a <space>

            if (article_tags.length < 5) {
                if (val.trim().length >= 3 && val.trim().length <= 20) {
                    let t = article_tags
                    if (t.indexOf(val.trim().toLowerCase()) === -1) {
                        setTags(t.concat(val.trim().toLowerCase()))
                    } else {
                        props.notyf.open({ type: "warn", message: "The tag has already been added." })
                    }
                } else {
                    props.notyf.error("Invalid tag")
                }
            } else {
                props.notyf.error("Only upto 5 tags are allowed on a single article.")
            }
            setTagsInput("")
        }
        else {
            setTagsInput(val)
        }
    }

    function handleTagRemoval(val) {
        let i = article_tags.indexOf(val)
        if (i !== -1) {
            let t = article_tags
            t.splice(i, 1)
            setTags(t)
            handleSave()
        } else {
            props.notyf.error("Tag not found")
        }


    }


    function handleOpen() {
        setShow(true)
    }

    function validatePublish() {
        if (data_editor.blocks.length > 0) {
            if (data_editor.blocks[0].type === "paragraph" || data_editor.blocks[0].type === "header") {
                handleOpen()
            } else {
                props.notyf.error("Invalid article title")
            }
        } else {
            props.notyf.error("Invalid article")
        }
    }
    function handleClose() {
        setShow(false)
    }
    async function handleSave() {
        const savedData = await instanceRef.current.save();
        if (savedData.blocks.length > 0) {
            setTitle(savedData.blocks[0].data.text)
            if (savedData.blocks.length > 1) {
                setPreview(savedData.blocks[1].data.text)
            }
        } else {
            setTitle("No title")
        }
        setData(savedData)
        console.log("savedData", savedData);
    }

    function handleBtnClick() {
        handleSave()
        if (data_editor.blocks.length > 0) {

            if (data_editor.blocks[0].type === "paragraph" || data_editor.blocks[0].type === "header") {


                let _data_editor = {}
                if (props.mode === "edit") {
                    _data_editor.article_id = articleId
                } else if (props.mode === "new") {

                }

                if (banner !== "NA") {
                    _data_editor.article_banner = banner
                }
                _data_editor.xAuthToken = props.auth_token
                _data_editor.title = data_editor.blocks[0].data.text
                _data_editor.content = JSON.stringify(data_editor)
                _data_editor.preview = (data_editor.blocks.length >= 2) ? data_editor.blocks[1].data.text : data_editor.blocks[0].data.text
                _data_editor.tags = article_tags

                console.log({ _data_editor })

                Axios.post(`${getApiLink()}/api/v1/${api}`, _data_editor).then((response) => {
                    console.log(response)
                    if (response.data.success) {
                        if (props.mode === "edit") {
                            props.notyf.success("Saved article")

                        } else {
                            props.notyf.success("Published article")
                        }
                        setRedirect(
                            <>
                                <Redirect to={(() => `/read/${response.data.message.data.article_id}`)()} />
                            </>
                        )
                    }
                }).catch(e => {
                    if (e) {
                        console.log({ resp: e.response.data })
                        if (e.response.data.error.message !== null) {
                            props.notyf.error("" + e.response.data.error.message)
                        } else {
                            props.notyf.error("Error occured")
                        }
                    } else {
                        props.notyf.error("Error occured")
                    }
                })

            } else {
                props.notyf.error("Invalid article title")
            }
        } else {
            props.notyf.error("Invalid article")
        }
    }

    function handleBannerClick() {
        if (banner === "NA") {
            setBanner(getRandomBanner())
        } else {
            setBanner("NA")
        }
    }
    useEffect(() => {
        if (props.mode === "edit") {
            setMode("Save")
            setApi("updateArticleById")
            if (props.articleData) {
                setData(JSON.parse(props.articleData.content))
            } else {
                setData({ time: Date.now(), blocks: [], version: "2.17.0" })
            }

        } else {
            setMode("Publish")
            setApi("createArticle")
            setData({ time: Date.now(), blocks: [], version: "2.17.0" })
            setBanner("NA")
        }
    }, [])


    useEffect(() => {
        if (props.articleData) {
            setReady(true)
            setArticleId(props.articleData.article_id)
            setData(JSON.parse(props.articleData.content))
            setBanner(props.articleData.article_banner)

            if (props.articleData.tags) {
                let t = props.articleData.tags.map(i => i.tag)
                setTags(t)
            }

        }
    }, [props.articleData])

    return (<>
        {/* <br /> */}
        {redir}
        <div className="w-100 text-center" style={{
            // marginTop: "90px"
        }}>

            {(banner !== "NA") ? (<>

                {/* <div className="text-center mb-2 hide-xs hide-sm show-md show-lg show-xl">
                    <img src={banner} alt="&nbsp;" style={{
                        maxHeight: "20vw",
                        // maxWidth: "1200px",
                        width: "100vw",

                    }} />
                </div> */}

                <div className="w-100 show-xs show-sm hide-md">
                    <div className=" text-center mb-2" style={{
                        height: "40vw",
                        width: "100%",
                        overflow: "hidden",
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        marginTop: "15px",
                        borderBottomRightRadius: "18px",
                        borderBottomLeftRadius: "18px",

                    }}>
                        <div className="preload w-100 h-100">
                            <img
                                src={banner} alt="&nbsp;"
                                style={{
                                    height: "100%",
                                    width: "auto",
                                    borderBottomRightRadius: "18px",
                                    borderBottomLeftRadius: "18px",
                                }} />
                        </div>
                    </div>
                </div>
            </>) : (
                    <>
                        {/* <br /> */}
                    </>
                )}
        </div>

        <br />


        <div className="container">
            <div className="row">
                <div className="col-md-2">

                    {/* <hr className="light"/> */}
                    <button className="btn btn-primary mt-3" onClick={validatePublish}>
                        {mode}
                    </button>

                    <Modal show={show}
                        onHide={handleClose}
                        size="lg"
                    // dialogClassName="modal-90w"
                    >
                        <Modal.Header closeButton>
                            <Modal.Title>{mode} Article</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>

                            <h5>Article Card Preview</h5>
                            <Card className="mb-4" type={1} data={{
                                article_id: "test-article",
                                title: title,
                                time: Date.now(),
                                content: preview,
                                bannerLink: banner,
                                tags: article_tags,
                                user: {
                                    user_id: user_id,
                                    imageLink: user_dp,
                                    name: user_fullname,
                                    username: user_username
                                }
                            }} />


                            <hr className="light" />

                            <h5>Add tags</h5>

                            <input type="text" placeholder="enter tags" className="form-control" value={tagsInput} onChange={handleTagsAddition} />
                            <div className="text-muted" style={{ fontSize: "12px" }}>Press space to add tags</div>
                            <br />
                            <div className="tags">

                                {article_tags.map((i) => {

                                    let h = hexStrColor(i)
                                    let inv = invertColor(h, true)

                                    console.log({ h, inv })
                                    return (<>
                                        <div className="tag d-inline-block noselect" style={{
                                            backgroundColor: "#" + h,
                                            color: inv,
                                            cursor: "pointer"
                                        }} onClick={(() => handleTagRemoval(i))}>
                                            #{i}
                                        </div>
                                    </>)

                                })}

                                {(() => {
                                    if (article_tags.length > 0)
                                        return (
                                            <>
                                                <div className="text-muted" style={{ fontSize: "12px" }}>Click on the tags to remove them</div>
                                            </>
                                        )
                                })()}
                            </div>
                            <hr className="light" />
                            Would you like to {mode} this article.
                            <br />
                        </Modal.Body>
                        <Modal.Footer>
                            <Button variant="secondary" onClick={handleClose}>
                                Close
                            </Button>
                            <Button variant="primary" onClick={handleBtnClick}>
                                {mode}
                            </Button>
                        </Modal.Footer>
                    </Modal>

                    <button className="btn btn-outline-secondary btn-sm mt-1 show-xs show-sm hide-md" onClick={handleBannerClick}>
                        {(() => {
                            if (banner === "NA") {
                                return "Add Banner"
                            } else {
                                return "Remove Banner"
                            }
                        })()}
                    </button>
                    {/* <hr className="light"/> */}

                    {(() => {
                        if (banner !== "NA") {
                            return (
                                <>
                                    <button className="btn btn-sm btn-info show-xs show-sm hide-md" onClick={() => {
                                        setBanner(getRandomBanner())
                                    }}>Randomize</button>
                                </>
                            )
                        }
                    })()}
                </div>

                <div className="col-md-8">
                    <div className="w-100">
                        {(banner !== "NA") ? (<>

                            <div className="text-center mb-2 hide-xs hide-sm show-md show-lg show-xl">
                                <img src={banner} alt="&nbsp;"
                                    className="preload"
                                    style={{
                                        height: "20vw",
                                        // maxWidth: "1200px",
                                        width: "100%",
                                        borderBottomRightRadius: "18px",
                                        borderBottomLeftRadius: "18px",

                                    }} />
                            </div>

                            {/* <div className="w-100 show-xs show-sm hide-md">
                                <div className=" text-center mb-2" style={{
                                    height: "40vw",
                                    width: "100vw",
                                    overflow: "hidden",
                                    display: "flex",
                                    justifyContent: "center",
                                    alignItems: "center"

                                }}>
                                    <img src={banner} alt="&nbsp;" style={{
                                        height: "100%",
                                        width: "auto",
                                    }} />
                                </div>
                            </div> */}
                        </>) : (<></>)}
                    </div>

                    {(() => {
                        if ((props.mode === "edit" && ready) || (props.mode === "new")) {
                            return (
                                <>
                                    {(banner == "NA" || banner == "") ? <br /> : <></>}
                                    <EditorJs
                                        placeholder="Write an awesome..."
                                        instanceRef={instance => (instanceRef.current = instance)}
                                        onChange={handleSave}
                                        tools={EDITOR_JS_TOOLS}
                                        data={data_editor}
                                    />
                                </>
                            )
                        }
                    })()}

                </div>

                <div className="col-md-2">
                    <button className="btn btn-outline-secondary btn-sm mt-3 hide-xs hide-sm show-md" onClick={handleBannerClick}>
                        {(() => {
                            if (banner === "NA") {
                                return "Add Banner"
                            } else {
                                return "Remove Banner"
                            }
                        })()}
                    </button>

                    {(() => {
                        if (banner !== "NA") {
                            return (
                                <>
                                    <button className="btn btn-sm btn-info hide-xs hide-sm show-md mt-2" onClick={() => {
                                        setBanner(getRandomBanner())
                                    }}>Randomize</button>
                                </>
                            )
                        }
                    })()}


                </div>
            </div>

        </div>
    </>
    );
}

export default Editor;