import Axios from "axios"
import { getApiLink } from "./env.lib"
import Embed from '@editorjs/embed'
import Table from '@editorjs/table'
import Paragraph from '@editorjs/paragraph'
import List from '@editorjs/list'
import Warning from '@editorjs/warning'
import Code from '@editorjs/code'
import LinkTool from '@editorjs/link'
import Image from '@editorjs/image'
// import Raw from '@editorjs/raw'
import Header from '@editorjs/header'
import Quote from '@editorjs/quote'
import Marker from '@editorjs/marker'
import CheckList from '@editorjs/checklist'
import Delimiter from '@editorjs/delimiter'
import InlineCode from '@editorjs/inline-code'
import SimpleImage from '@editorjs/simple-image'

export const EDITOR_JS_TOOLS = {
    embed: {
        class: Embed,
        inlineToolbar: true,
        config: {
            services: {
                youtube: true,
                coub: true,
                imgur: true,
                twitch: true,
                gfycat: true,
                yandex: true,
                codepen: true,
                vimeo: true,
                // twitter:true,
                twitter: {
                    regex: /^https?:\/\/twitter\.com\/(?:#!\/)?(\w+)\/status(?:es)?\/(\d+)(?:\/.*)?$/,
                    embedUrl: 'https://twitframe.com/show?url=https://twitter.com/<%= remote_id %>',
                    html: '<iframe width="100%" height="600" style="margin: 0 auto;" frameborder="0" scrolling="no" allowtransparency="true"></iframe>',
                    height: 600,
                    width: 600,
                    id: ids => ids.join('/status/')
                },
                instagram: {
                    regex: /(https?:\/\/(?:www\.)?instagram\.com\/p\/([^/?#&]+)).*/,
                    embedUrl: '<%= remote_id %>',
                    html: '<div style="width: 100%; height: 100px; background-color: #DB2E6E; text-align: center; color: #ffffff; padding-top: 40px">Instagram embedded.</div>',
                    height: 300,
                    width: 600
                },

                // instagram: {
                //     regex: /https?:\/\/www\.instagram\.com\/p\/([^\/\?\&]+)\/?/,
                //     embedUrl: 'https://www.instagram.com/p/<%= remote_id %>/embed',
                //     html: '<iframe width="400" height="505" style="margin: 0 auto;" frameborder="0" scrolling="no" allowtransparency="true"></iframe>',
                //     height: 505,
                //     width: 400
                // },

            }
        }
    },
    table: {
        class: Table,
        inlineToolbar: true,
        config: {
            rows: 2,
            cols: 3,
        },
    },
    paragraph: {
        class: Paragraph,
        inlineToolbar: true
    },
    list: {
        class: List,
        inlineToolbar: true,
    },
    warning: {
        class: Warning,
        inlineToolbar: true,
        shortcut: 'CMD+SHIFT+W',
        config: {
            titlePlaceholder: 'Title',
            messagePlaceholder: 'Message',
        },
    },
    code: Code,
    // linkTool: LinkTool,
    linkTool: {
        class: LinkTool,
        config: {
            endpoint: `${getApiLink()}/linkToolUrl` , // Your backend endpoint for url data fetching
        }
    },
    // image: Image,
    image: {
        class: Image,
        config: {

            uploader: {
                uploadByFile(file) {
                    let f = new FormData()
                    f.append("file", file)
                    return Axios.post(`${getApiLink()}/api/v1/upload`, f, {

                    }).then((doc) => {
                        console.log(doc)

                        if (doc.data.success) {
                            return {
                                success: 1,
                                file: {
                                    url: `${getApiLink()}/file/${doc.data.file_name}`
                                }
                            }
                        } else {
                            return {
                                success: 0
                            }
                        }
                    }).catch((e) => {
                        console.log(e)
                        console.log("Error while uploading image")
                        return {
                            success: 0
                        }
                    })
                },
            }
        }
    },
    header: Header,
    quote: Quote,
    marker: Marker,
    checklist: CheckList,
    delimiter: Delimiter,
    inlineCode: InlineCode,
    simpleImage: SimpleImage
}