import React, { useEffect } from 'react';

import { Link } from "react-router-dom"

const Page404 = () => {

    useEffect(()=>{
        document.title = "Page not found - Lattice"
    },[])
    return (
        <div className="container">
            <div id="page-top">
                <div id="wrapper">
                    <div className="d-flex flex-column" id="content-wrapper">
                        <div id="content card-sh">
                            <div className="container-fluid">
                                <div className="text-center mt-5">
                                    <div className="error mx-auto" data-text="404"
                                        style={{ width: "297px", fontSize: "147px" }}>
                                        <p className="m-0">404</p>
                                    </div>
                                    <p className="text-dark mb-5 lead" style={{ color: "rgb(255,255,255)", fontSize: "50px" }}>Page
                                    Not Found
                                    </p>
                                    {/* color: "rgb(203,207,238)"  */}
                                    <p style={{}}>There seems to be a glitch in the Matrix</p>
                                    <Link
                                        to="/" style={{}}>← Back to Home</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Page404;